import React from "react";
import styles from "./skills.module.css";
import { skills } from "../../data/utils";
import Plot from "react-plotly.js";

const subSkills = skills.getSkillData("Language");
// console.log(subSkills.JavaScript);
const jsSkills = subSkills.JavaScript;
const { experience } = jsSkills;

export const SkillsPlot = () => {
  const oX = Object.keys(experience);
  const oY = Object.values(experience);
  return (
    <div className={styles.container}>
      <Plot
        data={[
          {
            x: oX,
            y: oY,
            type: "scatter",
            mode: "lines+markers",
            marker: { color: "blue" },
          },
        ]}
        layout={{
          title: "Language",
          width: 640,
          height: 480,
        }}
      />
    </div>
  );
};

const SkillsPlotPage = () => <SkillsPlot></SkillsPlot>;

export default SkillsPlotPage;
